export interface ILocalCredentials {
    loginDomain: string;
    b2cTenant: string;
    nativeApplicationId: string;
    ropcUserFlowName: string;
    userImpersonationScopeURL: string;
    defaultUser: ILocalCredentialsUser;
    additionalUsers: ILocalCredentialsUser[];
    // Access token of currently signed in user
    accessToken?: string;
    // Unix timestamp of expiry date in seconds
    expiryDate?: number;
    // Name of current signed in mock user
    currentUser?: string;
    // Customer account number of signed in mock user
    customerAccountNumber?: string;
}

export interface ILocalCredentialsUser {
    name: string;
    email: string;
    password: string;
    customerAccountNumber?: string;
}
